//Mixins -----------------------------------------------------------
@mixin flex-space {
  display: flex;
  justify-content: space-between;
}

//General --------------------------------------------------------
html {
  height: 100%;
}

body {
  min-height: 100%;

  display: flex;
  flex-direction: column;
}

/*Header*/
header {
  background-color: $light;
  width: 100%;

  box-shadow: 2px 2px 15px $accent-dark;
  border-top: 10px solid $dark;

  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
}

.logoPic {
  display: block;
  height: 50px;
  margin: 0 auto;
}

/*Footer*/
footer {
  background-color: $light;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding: 1em 0;

  margin-top: auto;
}

footer div {
  display: flex;
  justify-content: center;
  margin: 0.5em 1em;
}

/*Main*/
main {
  width: 50%;
  margin: 5em auto;
}

section {
  margin: 1em 0;
  padding: 5em 2em;
  border-radius: 15px;

  border-left: 10px solid $accent-dark;
  border-right: 10px solid $accent-dark;
}

#content {
  @include flex-space;
}

.button {
  width: 30%;
  padding: 1rem 0;
  border-radius: 15px;
}

.wrapper {
  width: 100%;
}

img {
  display: block;
  width: auto;
  height: 40px;
}

.pageHeader {
  @include flex-space;
  padding-bottom: 1em;
  margin-bottom: 5em;
  border-bottom: 10px solid $accent-dark;
}

.flex {
  display: flex;
  width: 100%;
  margin: 0 auto;
  justify-content: flex-end;
  align-items: center;
}
