//Form general -------------------------

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: 1.5rem;

  border: 10px solid $accent-dark;
  //border-right: 10px solid $accent-dark;
}

input,
label {
  width: 50%;
}

label {
  margin: 1.5em 0 0.5em 0;
}

input {
  padding: 1em;
}

input[type="submit"] {
  font-size: 1.5rem;
  margin: 2em 0;
}

//Add/Update form -------------------------
#updateForm {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#addForm {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.formWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30%;
  margin: 0 auto;
}

.closeButton,
#closeAdd,
#closeUp {
  margin: -1em 0 5em 0;
  font-size: 1rem;
}
