/*Media queries -----------------------------------------------------*/

/*Max 1750 px ---------------------------------------------*/
@media screen and (max-width: 1750px) {
  /*General*/
  main {
    width: 80%;
  }

  /*Form*/
  .formWrapper {
    width: 60%;
  }
}

/*Max 1000 px----------------------------------------------*/
@media screen and (max-width: 1000px) {
  /*General*/
  main {
    width: 95%;
  }

  #content {
    flex-direction: column;
  }

  /*First page*/
  .button {
    width: 100%;
    margin: 1rem 0;
  }

  /*Form*/
  .formWrapper {
    width: 95%;
  }

  input,
  label {
    width: 80%;
  }

  /*Burger*/
  .nav-links {
    width: 75%;
  }

  /*Table*/
  th {
    font-size: 0.8rem;
  }

  td {
    font-size: 0.7rem;
  }

  img {
    height: 25px;
  }
}
