/*COLOUR----------------------------------------------------------*/

//Structure -------------------------------------------------------
body {
  background-color: $dark;
  color: $dark;
}

header {
  background-color: $light;
}

/*Footer --------------------------------------------------------*/
footer {
  background-color: $light;
}

section {
  background-color: $light;
}

.button {
  background-color: $accent-dark;
}

//Typography ------------------------------------------------------
h1 {
  color: $dark;
}

.button h2 {
  color: $light;
}

h2,
h3,
h4,
h5,
h6 {
  color: $dark;
}

//Links --------------------------------------------------------
a {
  color: $accent-dark;
}

a:hover {
  color: $accent-light;
}

//Form -----------------------------------------------------------
input,
label {
  color: $dark;
}

#add,
.add,
#up,
#portfolioUp,
#workUp,
#login {
  color: $light;
  background-color: $blue;
}

#add:hover,
.add:hover,
#up:hover,
#portfolioUp:hover,
#workUp:hover,
#login:hover {
  background-color: $light-blue;
  color: $light;
}

.closeButton,
#closeUp {
  background-color: $accent-dark;
  color: $light;
}

.closeButton:hover,
#closeUp:hover {
  background-color: $accent-light;
  color: $light;
}

#updateForm,
#addForm {
  background-color: rgba(7, 7, 29, 0.925);
}

.formWrapper {
  background-color: $light;
}

#error p {
  color: red;
}

form h2 {
  color: $dark;
}

//Button ---------------------------------------------------------
button {
  background-color: $light;
  border: none;
  cursor: pointer;
}
