/*Burger -------------------------------------*/
.burgernav {
  display: none;
  cursor: pointer;
  position: absolute;
  right: 25px;
  top: 10px;
}

.burgernav div {
  width: 25px;
  height: 3px;
  background-color: $dark;
  margin: 5px;
  transition: all 0.3s ease;
}

ul {
  justify-content: space-around;
  padding-top: 10%;
  padding-bottom: 20%;

  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-top: 10px solid $light;
  border-left: 10px solid $accent-dark;
  border-bottom: 10px solid $light;

  box-shadow: 12px 12px 15px $accent-dark;
}

ul a {
  color: $light;
  font-size: 2rem;
}

body {
  overflow-x: hidden;
}
.nav-links {
  position: absolute;
  right: 0px;
  height: 93.5vh;
  top: 4.8vh;
  background-color: $dark;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 35%;
  transform: translateX(100%);
  transition: transform 0.5s ease-in;
}
.nav-links li {
  opacity: 0;
  margin: 1rem;
}
.burgernav {
  display: block;
}

/*Changes the look of the burger nav. Nicer transitions.*/
.nav-active {
  transform: translateX(0%);
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

/*Toggles the lines in the burger nav, used with JS*/
.toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}
